
.container {
	padding-left: 20px;
	padding-right: 20px;
	margin-left: auto;
	margin-right: auto;
	@media screen and (min-width: 480px) {
		width: 480px;
	}
	@media screen and (min-width: 768px) {
		width: 768px;
		padding-left: 32px;
		padding-right: 32px;
	}
	@media screen and (min-width: 1200px) {
		width: 1200px;
		padding-left: 16px;
		padding-right: 16px;
	}
}