.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .5s linear, visibility .5s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  background: #fff;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .modal {
    min-width: 568px;
    min-height: 468px;
    padding: 32px 60px;
