.slider {
  height: inherit;
  width: inherit;
  background-color: #84a178;
  border-radius: 24px;
  position: relative;
  overflow: hidden;
}

.wrapper > input {
  display: none;
}

.slides {
  height: inherit;
  width: inherit;
  position: absolute;
}

.slide1 {
  background-color: #84a178;
}

.slide2 {
  background-color: #8a2be2;
}

.slide3 {
  background-color: #b8860b;
}

.wrapper .controls {
  justify-content: center;
  display: flex;
}

.wrapper label {
  cursor: pointer;
  width: 12px;
  -o-border-radius: 50%;
  background: #d9d9d9;
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

.wrapper label:after {
  content: " ";
  height: 12px;
  width: 12px;
  -o-border-radius: 50%;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: -4px;
  left: -4px;
}

.wrapper label {
  cursor: pointer;
  height: 12px;
  width: 12px;
  -o-border-radius: 50%;
  -o-transition: background ease-in-out .5s;
  border-radius: 50%;
  transition: background .5s ease-in-out;
  display: inline-block;
  position: relative;
}

.wrapper label:not(:last-child) {
  margin-right: 18px;
}

.wrapper label:hover, #slide1:checked ~ .controls label:nth-of-type(1), #slide2:checked ~ .controls label:nth-of-type(2), #slide3:checked ~ .controls label:nth-of-type(3) {
  background-color: #84a178;
}

.slides {
  height: inherit;
  opacity: 0;
  width: inherit;
  z-index: 0;
  -o-transition: transform ease-in-out .5s, opacity ease-in-out .5s;
  transition: transform .5s ease-in-out, opacity .5s ease-in-out;
  position: absolute;
  transform: scale(1.5);
}

#slide1:checked ~ .slider > .slide1, #slide2:checked ~ .slider > .slide2, #slide3:checked ~ .slider > .slide3 {
  opacity: 1;
  z-index: 1;
  transform: scale(1);
}

